import {
	getTraining,
} from '@/api/job'
import citys from '@/utils/data'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
import {
	mapGetters
} from 'vuex'
export default {
	computed: {
		...mapGetters([
			'address',
		])
	},
	data() {
		return {
			ready: false,
			filter: {
				active: null,
				type: {
					items: [{
							text: '上岗培训',
							id: 1,
						},
						{
							text: '就业培训',
							id: 2,
						},
						{
							text: '学历培训',
							id: 3
						},
						{
							text: '不限',
							id: 0,
						}
					],
					show: false,
					text: null,
					id: null,
				},
				time: {
					items: [{
							text: '近三天',
							id: 3,
						},
						{
							text: '近一周',
							id: 7,
						},
						{
							text: '近一月',
							id: 30
						},
						{
							text: '不限',
							id: 0,
						}
					],
					show: false,
					text: null,
					id: null,
				},
			},
			list: null,
			count: null,
			search: {
				time: 0,
				type: 0,
				count: true,
				page: 1,
				limit: 10
			},
			locations: {
				province: null,
				city: null,
				area: null,
				citys: '全国',
			},
			city: {
				show: false,
				options: citys
			},
			showlist: false
		}
	},
	onReady() {
		this.ready = true
	},
	onShow() {
		this.showlist = true
		this.getTraining();
	},
	onPullDownRefresh() {
		this.refresher()
		uni.stopPullDownRefresh();
	},
	onReachBottom() {
		this.refresher()
	},
	methods: {
		getTraining() {
			const s = this.search
			const t = this
			const a = this.address
			let sl = this.showlist
			const search = Object.assign({}, {
					count: s.count,
					page: sl ? 1 : s.page,
					limit: sl ? 10 * s.page : s.limit,
				},
				s.time ? {
					time: s.time
				} : {},
				s.type ? {
					type: s.type
				} : {},
				s.value ? {
					value: s.value
				} : {},
				a.province ? {
					province: a.province
				} : {},
				a.city ? {
					city: a.city
				} : {},
				a.area ? {
					area: a.area
				} : {},
			);
			getTraining(search).then(response => {
				let r = response.data
				if (response.code === 20000) {
					t.count = r.list.count
					if (t.list && t.list.length > 0 && !sl) {
						t.list = t.list.concat(r.list.rows)
					} else {
						sl = false
						t.list = r.list.rows
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		refresher() {
			let t = this
			if (t.count > t.list.length) {
				t.search.page = t.search.page++
				this.getTraining()
			} else {
				$wuxToast().hide()
				$wuxToast().show({
					type: 'forbidden',
					text: '已加载全部'
				})
			}
		},
		editCity(e) {
			let s = this.locations
			let address = e.detail.options.map((n) => n.label)
			let arr = e.detail.options.map((n) => n.label)
			if (arr.length === 1) {
				s.citys = address[0]
			} else if (arr.length === 2) {
				s.citys = address[1]
			} else if (arr.length === 3) {
				s.citys = address[2]
			}
			s.province = arr[0]
			s.city = arr[1]
			s.area = arr[2]
			this.$store.commit('SET_ADDRESS', s)
			this.list = null
			this.getTraining()
		},
		filterClose(n) {
			let f
			if (n === 'type') {
				f = this.filter.type
			} else if (n === 'time') {
				f = this.filter.time
			}
			f.show = false
			this.filter.active = null
		},
		filterClick(t, i, n) {
			let p
			if (n === 'type') {
				p = this.filter.type
			} else if (n === 'time') {
				p = this.filter.time
			}
			p.text = t
			p.id = i
		},
		upType() {
			const f = this.filter
			this.search.type = f.type.id ? f.type.id : 0
			f.active = null
			f.type.show = false
			this.list = null
			this.getTraining()
		},
		upTime() {
			const f = this.filter
			this.search.time = f.time.id ? f.time.id : 0
			f.active = null
			f.time.show = false
			this.list = null
			this.getTraining()
		},
		onChange(e) {
			const d = e.detail
			const f = this.filter
			f.active = d
			f.type.show = d === 0 ? true : false
			f.time.show = d === 1 ? true : false
		},
		detailTo(id) {
			uni.navigateTo({
				url: '/pages/training/detail?id=' + id
			});
		},
		getSearch(e) {
			setTimeout(() => {
				this.search.value = e.detail
				this.list = null
				this.getTraining()
			}, 500)
		}
	}
}
